@use 'global/mixins' as m;
@use 'global/variables' as v;

.show{
    position: absolute;
    top: 0;
    left: 0;
    background-color: v.$primary-light;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    .header-nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem;
        border-bottom: 1px solid v.$secondary-blue;
        p{
            color: v.$secondary-blue;
        }
        img{
            width: 2.5rem;
        }
    }
    nav{
        ul{
            margin: 0;
            padding: 0;
            li{
                padding: 2rem;
                border-bottom: 1px solid v.$secondary-blue;
            }
        }
    }
    .footer-nav{
        position: absolute;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 0 1.5rem;
        border-top: 1px solid v.$secondary-blue;
        p{
            color: v.$secondary-blue;
        }
        .icons-footer{
            height: 100%;
            a{
                padding: 2rem 1.5rem;
                &:last-child{
                    padding-right: 0;
                }
                border-left: 1px solid v.$secondary-blue;
            }
        }
    }
}
.hide{
    display: none;
}