@use 'global/mixins' as m;
@use 'global/variables' as v;

.colors{
    position: absolute;
    width: 100%;
    height: calc(100vh - 8rem);
    display: flex;
    justify-content: space-between;
    padding: 15rem;
    align-items: center;
    flex-direction: column;
}
.colors .color{
    filter: blur(8.5rem);
}

.colors .color:nth-child(1){
    background: v.$accent-green;
    width: 10rem;
    height: 10rem;
}
.colors .color:nth-child(2){
    background: v.$secondary-purple;
    width: 15rem;
    height: 15rem;
}
.presentation{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5rem;
    height: calc(100vh - 8rem);
    .iam{
        .hello{
            font-size: 1.8rem;
            font-weight: normal;
        }
        .myname{
            font-size: 6.2rem;
            font-weight: normal;
        }
        .developer{
            font-size: 2rem;
            color: v.$accent-green;
        }
    }
    .github{
        .github-profile{
            font-size: 1.4rem;
            color: v.$secondary-blue;
        }
        .const{
            font-size: 1.4rem;
            font-weight: normal;
            color: v.$secondary-purple;
            .githublink{
                color: v.$accent-green;
            }
            .link{
                color: v.$accent-red-light;
            }
            .equal{
                color: v.$secondary-white;
            }
        }
    }
}