html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #011627;
  color: white;
  font-family: "Fira Code", monospace;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
}

h1, h2 {
  font-family: "Fira Code", monospace;
  margin: 0;
  font-weight: bold;
  color: white;
}

p {
  color: white;
  font-size: 1.8rem;
}

h1 {
  font-size: 6.2rem;
}

h2 {
  font-size: 3.2rem;
}

.label {
  font-size: 1.6rem;
}

.code-snippet {
  font-size: 1.4rem;
}

.btn {
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 0.5rem;
  padding: 0.8rem 1rem;
  width: max-content;
}
.btn:hover {
  cursor: pointer;
}
.btn.btn-primary {
  background-color: #FEA55F;
  color: #01080E;
  border: none;
}
.btn.btn-primary:hover {
  background-color: #fec292;
}
.btn.btn-default {
  background-color: #1C2B3A;
  color: white;
  border: none;
}
.btn.btn-default:hover {
  background-color: #2d455c;
}
.btn.btn-ghost {
  background-color: transparent;
  border: 1px solid white;
  color: white;
}
.btn.btn-ghost:hover {
  border: 1px solid #e6e6e6;
}

.card {
  max-width: 20.9rem;
  border: 1px solid #607B96;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .card {
    max-width: 37rem;
  }
}
.card .header-card {
  position: relative;
}
.card .header-card img {
  border-radius: 1rem 1rem 0 0;
}
.card .header-card .icon-box {
  position: absolute;
  right: 0;
  top: 0;
  margin: 1.5rem;
}
.card .header-card .icon-box.react {
  background-color: #86E1F9;
  border-radius: 0;
}
.card .body-card {
  background-color: #011221;
  padding: 2rem;
  border-radius: 0 0 1rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card .body-card p {
  margin-top: 0;
  color: #607B96;
}

header {
  padding: 2rem;
  border-bottom: 1px solid #607B96;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header h1 {
  color: #607B96;
  font-weight: normal;
}
header .hamburger {
  width: 2.5rem;
}

.show {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #011627;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.show .header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  border-bottom: 1px solid #607B96;
}
.show .header-nav p {
  color: #607B96;
}
.show .header-nav img {
  width: 2.5rem;
}
.show nav ul {
  margin: 0;
  padding: 0;
}
.show nav ul li {
  padding: 2rem;
  border-bottom: 1px solid #607B96;
}
.show .footer-nav {
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 1.5rem;
  border-top: 1px solid #607B96;
}
.show .footer-nav p {
  color: #607B96;
}
.show .footer-nav .icons-footer {
  height: 100%;
}
.show .footer-nav .icons-footer a {
  padding: 2rem 1.5rem;
  border-left: 1px solid #607B96;
}
.show .footer-nav .icons-footer a:last-child {
  padding-right: 0;
}

.hide {
  display: none;
}

.colors {
  position: absolute;
  width: 100%;
  height: calc(100vh - 8rem);
  display: flex;
  justify-content: space-between;
  padding: 15rem;
  align-items: center;
  flex-direction: column;
}

.colors .color {
  filter: blur(8.5rem);
}

.colors .color:nth-child(1) {
  background: #43D9AD;
  width: 10rem;
  height: 10rem;
}

.colors .color:nth-child(2) {
  background: #4D5BCE;
  width: 15rem;
  height: 15rem;
}

.presentation {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5rem;
  height: calc(100vh - 8rem);
}
.presentation .iam .hello {
  font-size: 1.8rem;
  font-weight: normal;
}
.presentation .iam .myname {
  font-size: 6.2rem;
  font-weight: normal;
}
.presentation .iam .developer {
  font-size: 2rem;
  color: #43D9AD;
}
.presentation .github .github-profile {
  font-size: 1.4rem;
  color: #607B96;
}
.presentation .github .const {
  font-size: 1.4rem;
  font-weight: normal;
  color: #4D5BCE;
}
.presentation .github .const .githublink {
  color: #43D9AD;
}
.presentation .github .const .link {
  color: #E99287;
}
.presentation .github .const .equal {
  color: white;
}